.persons {
  display: flex;
  max-width: 1200px;
  margin: 0 20px 50px;
  flex-wrap: wrap;
  justify-content: center;
}

div.person-item {
  background-color: #eee8ec;
  flex-direction: column;
  padding: 2% 0 4% 0;
  align-items: center;
  margin-bottom: 20px;
  width: 400px;
  margin: 20px 10px;
}

div.person-item img {
  object-fit: contain;
  margin: 30px 0 20px 0;
  width: 200px;
}

div.person-info {
  color: black;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 20px 30px 20px;
}

div.person-info h3 {
  font-size: 1.7rem;
  margin: 0 auto;
}

div.person-info p {
  color: black;
}

@media only screen and (max-width: 768px) {
  div.person-item img {
    width: 150px;
  }
}
@media only screen and (max-width: 480px) {
  div.person-item {
    margin: 10px 0;
  }
  div.person-info h3 {
    font-size: 1.3rem;
  }
  div.person-item {
    width: unset;
  }
  div.person-item img {
    width: 100px;
  }
}
